<template>
    <div>
        <div class="row">
            <div class="col-md-12">
                <app-breadcrumbs :heading="clinic.name" sub-heading="Clinic">
                    <small slot="current"> {{clinic.name}}</small>
                </app-breadcrumbs>
            </div>
        </div>
        <div class="row">
            <div class="col-md-4">
                <div class="my-3 bg-white rounded box-shadow" v-if="clinic">
                    <h6 class="bg-light border-bottom border-gray pb-2 mb-0 p-3">Basic Information</h6>
                    <div class="media text-muted pt-3">
                        <p class="media-body pb-3 pl-3 mb-0 small lh-125 border-bottom border-gray">
                            <strong class="d-block text-gray-dark">Email</strong>
                            <a>{{clinic.email}}</a>
                        </p>
                    </div>
                    <div class="media text-muted pt-3">
                        <p class="media-body pb-3 pl-3 mb-0 small lh-125 border-bottom border-gray">
                            <strong class="d-block text-gray-dark">Phone Number</strong> {{clinic.phone}}
                        </p>
                    </div>
                    <div class="media text-muted pt-3">
                        <p class="media-body pb-3 pl-3 pb-2 mb-0 small lh-125 ">
                            <strong class="d-block text-gray-dark">Address</strong> {{clinic.address}}
                        </p>
                    </div>
                    <div class="bg-light px-3 py-2 text-muted text-right">
                        <span>
                            <small>
                                Expand
                            </small>
                        </span>
                    </div>
                </div>
            </div>
            <div class="col-md-4">
                <div class="my-3 bg-white rounded box-shadow">
                    <h6 class="bg-light border-bottom border-gray pb-2 mb-0 p-3">Patients</h6>
                    <div class="media text-muted p-3 border-bottom border-gray" v-for="patient in patients.slice(0,3)" v-bind:key="patient.id">
                        <img src="http://via.placeholder.com/32/FEC606/fff?text=A" alt="" class="mr-2 rounded">
                        <div class="media-body mb-0 small lh-125 ">
                            <div class="d-flex justify-content-between align-items-center w-100">
                                <strong class="text-gray-dark">{{patient.name}}</strong>
                                <router-link :to="'/patients/'+patient.id">View</router-link>
                            </div>
                            <span class="d-block text-uppercase">{{patient.gender}}</span>
                        </div>
                    </div>
                    <div class="bg-light px-3 py-2 text-muted text-right">
                        <router-link :to="'/patients'">
                            <small>
                                View All
                            </small>
                        </router-link>
                    </div>
                </div>
            </div>
            <div class="col-md-4">
                <div class="my-3 bg-white rounded box-shadow">
                    <h6 class="bg-light border-bottom border-gray pb-2 mb-0 p-3">Tests</h6>
                    <div class="media text-muted p-3 border-bottom border-gray">
                        <img src="http://via.placeholder.com/32/112233/fff?text=+" alt="" class="mr-2 rounded">
                        <div class="media-body mb-0 small lh-125 ">
                            <div class="d-flex justify-content-between align-items-center w-100">
                                <strong class="text-gray-dark">Aida Gutierrez</strong>
                                <span class="text-muted">View</span>
                            </div>
                            <span class="d-block">@username</span>
                        </div>
                    </div>
                    <div class="media text-muted p-3 border-bottom border-gray">
                        <img src="http://via.placeholder.com/32/34495E/fff?text=+" alt="" class="mr-2 rounded">
                        <div class="media-body mb-0 small lh-125">
                            <div class="d-flex justify-content-between align-items-center w-100">
                                <strong class="text-gray-dark">Hyde Dunlap</strong>
                                <span class="text-muted">View</span>
                            </div>
                            <span class="d-block">@username</span>
                        </div>
                    </div>
                    <div class="media text-muted p-3">
                        <img src="http://via.placeholder.com/32/25373D/fff?text=+" alt="" class="mr-2 rounded">
                        <div class="media-body mb-0 small lh-125">
                            <div class="d-flex justify-content-between align-items-center w-100">
                                <strong class="text-gray-dark">Harrington Wyatt</strong>
                                <span class="text-muted">View</span>
                            </div>
                            <span class="d-block">@username</span>
                        </div>
                    </div>
                    <div class="bg-light px-3 py-2 text-muted  text-right">
                        <span>
                            <small>
                                View All
                            </small>
                        </span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapGetters } from "vuex";
import AppBreadCrumbs from "@/components/AppBreadCrumbs";
export default {
  components: {
    "app-breadcrumbs": AppBreadCrumbs
  },
  computed: {
    ...mapGetters("clinicsModule", ["clinic"]),
    ...mapGetters("patientsModule", ["patients"])
  },
  methods: {
    loadClinicDetail() {
      this.$store.dispatch("clinicsModule/fetchClinic", this.$route.params.id);
      this.$store.dispatch(
        "patientsModule/fetchPatients",
        this.$route.params.id
      );
    }
  },
  watch: {
    $route(to, from) {
      this.loadClinicDetail();
    }
  },
  created() {
    this.loadClinicDetail();
  }
};
</script>



