import axios from "axios";

axios.defaults.baseURL =
  "https://my-json-server.typicode.com/zainzafar90/fake-json-server";

const apiService = {
  getOrganizations() {
    return new Promise((resolve, reject) => {
      axios
        .get(`organizations`)
        .then(data => {
          resolve(data);
        })
        .catch(error => {
          reject(error);
        });
    });
  },
  getOrganization(organizationId) {
    return new Promise((resolve, reject) => {
      axios
        .get(`organizations/${organizationId}`)
        .then(data => {
          resolve(data);
        })
        .catch(error => {
          reject(error);
        });
    });
  },
  getPatients(organizationId) {
    return new Promise((resolve, reject) => {
      axios
        .get(`patients`)
        .then(data => {
          resolve(data);
        })
        .catch(error => {
          reject(error);
        });
    });
  },
  getPatient(patientId) {
    return new Promise((resolve, reject) => {
      axios
        .get(`patients/${patientId}`)
        .then(data => {
          resolve(data);
        })
        .catch(error => {
          reject(error);
        });
    });
  }
};

// Add a request interceptor
axios.interceptors.request.use(
  function(config) {
    $.blockUI({
      message: "<img src='https://media1.giphy.com/media/3oEjI6SIIHBdRxXI40/giphy-preview.gif' height='128' width='128'/>",
      overlayCSS: {
        backgroundColor: "#fff",
        opacity: 0.8,
        cursor: "wait"
      },
      css: {
        border: 0,
        padding: 0,
        backgroundColor: "transparent"
      }
    });
    return config;
  },
  function(error) {
    // Do something with request error
    return Promise.reject(error);
  }
);

// Add a response interceptor
axios.interceptors.response.use(
  function(response) {
    $.unblockUI();
    return response;
  },
  function(error) {
    return Promise.reject(error);
  }
);
export default apiService;



