<template>
    <div>
        <nav aria-label="breadcrumb">
            <ol class="breadcrumb  mt-2 bg-white">
                <li class="breadcrumb-item" v-if="!clinics">
                    <router-link to="/"><small>  Clinics </small></router-link>
                </li>
                <li class="breadcrumb-item" v-if="clinic">
                    <slot name="clinic"></slot>
                </li>
                <li class="breadcrumb-item" v-if="patients">
                    <router-link to="/patients"><small>  Patients </small></router-link>
                </li>
                <li class="breadcrumb-item" v-if="patient">
                    <slot name="patient"></slot>
                </li>
                <li class="breadcrumb-item active" aria-current="page">
                    <slot name="current"></slot>
                </li>
            </ol>
        </nav>
        <div class="bg-cyan my-1 p-2 rounded box-shadow">
            <div class="lh-100">
                <h3 class="text-white lh-100">{{heading}}</h3>
                <small class="text-white text-uppercase">{{subHeading}}</small>
            </div>
        </div>
    </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  props: ["heading","subHeading","clinics", "clinic", "patients", "patient"]
};
</script>
<style scoped>
.bg-cyan {
  background-color: #17a2b8;
}
</style>




