<template>
    <div>
        <div class="row">
            <div class="col-md-12">
                <app-breadcrumbs heading="Patients" sub-heading="All Patient" :clinic="true">
                    <router-link :to="'clinics/'+clinic.id" slot="clinic"><small>  {{clinic.name}} </small></router-link>
                    <small slot="current"> Patients</small>
                </app-breadcrumbs>
            </div>
        </div>
        <div class="row">
            <div class="col-md-12">
                <div class="my-3 bg-white rounded box-shadow">
                    <div class="media text-muted p-3 border-bottom border-gray" v-for="patient in patients" v-bind:key="patient.id">
                        <img :src="getImageUrl(patient.age)" alt="" class="mr-2 rounded">
                        <div class="media-body mb-0 small lh-125 ">
                            <div class="d-flex justify-content-between align-items-center w-100">
                                <h6 class="text-gray-dark mb-0">{{patient.name}} ({{getGender(patient.gender)}})</h6>
                                <router-link :to="'/patients/'+patient.id">View</router-link>
                            </div>
                            <span class="d-block text-info">{{patient.phone}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapGetters } from "vuex";
import AppBreadCrumbs from "@/components/AppBreadCrumbs";
export default {
  components: {
    "app-breadcrumbs": AppBreadCrumbs
  },
  computed: {
    ...mapGetters("clinicsModule", ["clinic"]),
    ...mapGetters("patientsModule", ["patients"])
  },
  created() {
    this.$store.dispatch("patientsModule/fetchPatients", this.$route.params.id);
  },
  methods: {
    getImageUrl(age) {
      return `http://via.placeholder.com/32/112233/fff?text=${age}`;
    },
    getGender(gender) {
      return gender === "male" ? "M" : "F";
    }
  }
};
</script>



