<template>
    <div>
         <div class="row">
            <div class="col-md-12">
               <app-breadcrumbs heading="Clinics" sub-heading="All Clinics" :clinics="true">
                 <small slot="current">Home</small>
               </app-breadcrumbs>
            </div>
        </div>
        <div class="row">
            <div class="col-md-12">
                <div class="my-3 bg-white rounded box-shadow">
                    <div class="media text-muted p-3 border-bottom border-gray" v-for="clinic in clinics" v-bind:key="clinic.id">
                        <img src="http://via.placeholder.com/32/112233/fff?text=+" alt="" class="mr-2 rounded">
                        <div class="media-body mb-0 small lh-125 ">
                            <div class="d-flex justify-content-between align-items-center w-100">
                                <h6 class="text-gray-dark mb-0">{{clinic.name}}</h6>
                                <router-link :to="'clinics/' + clinic.id">View</router-link>
                            </div>
                            <span class="d-block text-info">{{clinic.address}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapGetters } from "vuex";
import AppBreadCrumbs from "@/components/AppBreadCrumbs";
export default {
  components: {
    "app-breadcrumbs": AppBreadCrumbs
  },
  computed: {
    ...mapGetters("clinicsModule", ["clinics"])
  },
  created() {
    this.$store.dispatch("clinicsModule/fetchClinics");
  }
};
</script>



