import apiService from "../api";

const state = {
  patients: [],
  patient: {}
};

const getters = {
  patients: state => state.patients,
  patient: state => state.patient
};

const actions = {
  fetchPatients(context, organizationId) {
    apiService.getPatients(organizationId).then(response => {
      context.commit("setPatients", { patients: response.data });
    });
  },
  fetchPatient(context, patientId) {
    apiService.getPatient(patientId).then(response => {
      context.commit("setPatient", { patient: response.data });
    });
  }
};

const mutations = {
  setPatients(state, payload) {
    state.patients = payload.patients;
  },
  setPatient(state, payload) {
    state.patient = payload.patient;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};



