import Vue from "vue";
import Vuex from "vuex";
import clinicsModule from "./clinics";
import patientsModule from "./patients";

Vue.use(Vuex);

const store = new Vuex.Store({
  modules: {
    clinicsModule,
    patientsModule
  }
});

export default store;



