import apiService from "../api";

const state = {
  clinics: [],
  clinic: {}
};

const getters = {
  clinics: state => state.clinics,
  clinic: state => state.clinic
};

const actions = {
  fetchClinics(context) {
    apiService.getOrganizations().then(response => {
      context.commit("setClinics", { clinics: response.data });
    });
  },
  fetchClinic(context, clinicId) {
    apiService.getOrganization(clinicId).then(response => {
      context.commit("setClinic", { clinic: response.data });
    });
  }
};

const mutations = {
  setClinics(state, payload) {
    state.clinics = payload.clinics;
  },
  setClinic(state, payload) {
    state.clinic = payload.clinic;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};



