<template>
    <div>
        <div class="row">
            <div class="col-md-12">
                <app-breadcrumbs :heading="patient.name" sub-heading="Patient" :clinic="true" :patients="true">
                    <router-link :to="'/clinics/'+patient.organizationId" slot="clinic"><small>  {{patient.organizationName}} </small></router-link>
                    <router-link to="/patients" slot="patients"><small> Patients </small></router-link>
                    <small slot="current"> {{patient.name}}</small>
                </app-breadcrumbs>
            </div>
        </div>
        <div class="row">
            <div class="col-md-4">
                <div class="my-3 bg-white rounded box-shadow" v-if="patient">
                    <h6 class="bg-light border-bottom border-gray pb-2 mb-0 p-3">Basic Information</h6>
                    <div class="media text-muted pt-3">
                        <p class="media-body pb-3 pl-3 mb-0 small lh-125 border-bottom border-gray">
                            <strong class="d-block text-gray-dark">Full Name</strong>
                            <a>{{patient.name}}</a>
                        </p>
                    </div>
                    <div class="media text-muted pt-3">
                        <p class="media-body pb-3 pl-3 mb-0 small lh-125 border-bottom border-gray">
                            <strong class="d-block text-gray-dark">Phone Number</strong> {{patient.phone}}
                        </p>
                    </div>
                    <div class="media text-muted pt-3">
                        <p class="media-body pb-3 pl-3 pb-2 mb-0 small lh-125 ">
                            <strong class="d-block text-gray-dark">Gender</strong>
                            <span class="text-uppercase">{{patient.gender}}</span>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapGetters } from "vuex";
import AppBreadCrumbs from "@/components/AppBreadCrumbs";
export default {
  components: {
    "app-breadcrumbs": AppBreadCrumbs
  },
  computed: {
    ...mapGetters("patientsModule", ["patient"])
  },
  methods: {
    loadPatientDetail() {
      this.$store.dispatch(
        "patientsModule/fetchPatient",
        this.$route.params.id
      );
    }
  },
  watch: {
    $route(to, from) {
      console.log(to, from);
      this.loadPatientDetail();
    }
  },
  created() {
    this.loadPatientDetail();
  }
};
</script>



